import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView.vue'
import StudyView from '../views/StudyView.vue'
import LoginView from '../views/LoginView.vue'
import SignUpView from '../views/SignUpView.vue'
import SearchView from '../views/SearchView.vue'
import SearchKeywordGroup from '../components/SearchKeywordGroup.vue'
import AdminView from '../views/AdminView.vue'  
import IntroView from '../views/IntroView.vue' 
import UserView from '../views/UserView.vue'
import SearchBook from '../components/SearchBook.vue'
import SearchKeyword from '../components/SearchKeyword.vue'
import SearchWord from '../components/SearchWord.vue'
import ResourceView from '../views/ResourceView.vue'
import ManualView from '../views/ManualView.vue'

const routes = [
  { path: '/', name: 'intro', component: IntroView, meta: {
    layout: 'IntroLayout'
  } },
  { path: '/home', name: 'home', component: HomeView, meta: {
    layout: 'DefaultLayout'
  } },
  { path: '/about', name: 'about', component: AboutView },
  { path: '/study', name: 'study', component: StudyView },
  { path: '/login', name: 'login', component: LoginView },
  { path: '/signup', name: 'signup', component: SignUpView },
  { path: '/search', name: 'search', component: SearchView },
  { path: '/admin', name: 'admin', component: AdminView },
  { path: '/user', name: 'user', component: UserView },
  { path: '/search-book', name: 'search-book', component: SearchBook },
  { path: '/search-keyword', name: 'search-keyword', component: SearchKeyword },
  { path: '/search-keyword-group', name: 'search-keyword-group', component: SearchKeywordGroup },
  { path: '/search-word', name: 'search-word', component: SearchWord },
  { path: '/resource', name: 'resource', component: ResourceView },
  { path: '/manual', name: 'manual', component: ManualView },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
